import React, { Component } from 'react'
import './Card.css'

export default class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {link: props.link}
    this.openCardLink = this.openCardLink.bind(this)
  }
  openCardLink() {
    document.getElementById(this.state.link.id).click();
  }
  render() {
    return (
      <div className='card' style={{ backgroundImage: 'url(' + this.state.link.image + ')'}} onClick={this.openCardLink}>
        <a id={this.state.link.id} href={this.state.link.url} style={{display: 'none'}}>link</a>
        <div className='card-content'>
          <span className='card-title'>
            {this.state.link.title}
          </span>
          <div className='card-description'>
            <span className='card-description-text'>
              { this.state.link.description }
            </span>
          </div>
        </div>
      </div>
    )
  }
}
